@import '../../../node_modules/vazirmatn/Vazirmatn-font-face.css';
.linear-scale-container {
    position: relative; /* Make this container relative for absolute positioning of green line */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    direction: ltr;
}
.linear-scale {
    flex: 1;
    margin-right: 10px;
    position: relative;
    min-width: 100px;
}
.range-values {
    position: absolute;
    top: -20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 1.1em;
    font-weight: bold;
}

.range-input {
    width: 100%;
    background: transparent;
    -webkit-appearance: none;
    appearance: none;
    position: absolute;
    top: 0;
    left: 0;
}

.range-input::-webkit-slider-runnable-track {
    width: 100%;
    height: 4px;
    background: blue;
    -webkit-appearance: none;
}
 
.range-input::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    background: red;
    cursor: pointer;
    border-radius: 50%;
    position: relative;
    z-index: 3;
}
.range-value-display {
    position: absolute;
    top: -30px; /* Adjust to position the text above the handle */
    left: 50%;
    transform: translateX(-50%);
    color: white;
    font-weight: bold;
    background-color: blue;
    border-radius: 50%;
    padding: 2px 6px;
    white-space: nowrap;
    font-size: 0.9em;
    font-weight: bold;
    pointer-events: none;
    user-select: none;
}
.radio-container {
    display: flex;
    flex-direction: column; /* Stack items vertically */
}
.radio-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px; /* Space between each row */
}

.radio-item label {
    margin-left: 8px; /* Adjust spacing between checkbox and label */
    cursor: pointer; /* Change cursor to pointer when hovering over the label */
}
td {
    height: 80px;
    align-content:center;
}
h2 {
    margin: 0 0;
    font-size: smaller;
    font-weight: bold;
    max-height: 40px;
}

input {
    max-width: 70px;
}

